/* Skills */
*,
*::after,
*::before {
  box-sizing: border-box;
}

.u-center {
  align-content: center;
  display: flex;
  justify-content: center;
  height: 100%;
  padding: 0 5rem;
}

.skills {
  align-self: center;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.skill {
  margin: 0.5em 0;
  position: relative;
  width: 100%;
}

.skill__type {
  color: #fff;
  font-weight: 300;
  font-size: 0.85rem;
  letter-spacing: 0.145em;
  text-transform: uppercase;
}

.skill__outer-bar, .skill__inner-bar {
  height: 0.75em;
}

.skill__outer-bar {
  background: #333;
}

.skill__inner-bar {
  -webkit-animation: fillBar 1s forwards;
          animation: fillBar 1s forwards;
  background: #fff;
  left: 5em;
  transform: scaleX(0);
  transform-origin: 0 0;
}

@-webkit-keyframes fillBar {
  to {
    transform: scaleX(1);
  }
}

@keyframes fillBar {
  to {
    transform: scaleX(1);
  }
}

